<template>
  <div id="contract-crate">
    <div class="row">
      <div class="col-12">
        <div class="card border-0">
          <div class="card-body">
            <!-- {{ Contract.contract_date }} -->

            <h1 class="fw-bold title-page">Lp hp ng</h1>
            <div class="row">
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="yourInput">Danh sch cng ty</label>
                  <div class="input-group">
                    <input
                      disabled
                      type="text"
                      class="form-control"
                      placeholder="Danh sch cng ty"
                      v-model.lazy="Contract.company_name"
                    />

                    <button
                      class="btn btn-outline-secondary"
                      type="button"
                      id="button-addon3"
                      @click="openModalCompany"
                    >
                      <i class="fa-thin fa-magnifying-glass"></i>
                    </button>
                  </div>
                </div>
              </div>
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="yourInput">u mi lin h</label>
                  <div class="input-group">
                    <input
                      disabled
                      type="text"
                      class="form-control"
                      placeholder="u mi lin h"
                      v-model.lazy="Contract.customer_name"
                    />
                    <button
                      class="input-group-text"
                      @click="addCustomer"
                      title="Thm ngi lin h"
                    >
                      <i class="fa-solid fa-plus"></i>
                    </button>
                    <button
                      class="btn btn-outline-secondary"
                      type="button"
                      id="button-addon2"
                      @click="openModalCustomer"
                    >
                      <i class="fa-thin fa-magnifying-glass"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">S hp ng</label>
                  <div class="input-group mb-3">
                    <input
                      v-model.lazy="Contract.contract_number"
                      placeholder="Nhp s hp ng"
                      type="text"
                      class="form-control"
                      aria-label="Text input with segmented dropdown button"
                      required
                    />

                    <select
                      v-model="Contract.contract_number_pattern"
                      class="form-select"
                      id="inputGroupSelect01"
                    >
                      <option
                        v-for="item in selects.contract_number_pattern_list"
                        :key="item"
                      >
                        {{ item }}
                      </option>
                    </select>
                  </div>
                </div>
              </div>
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Ngy hp ng</label>
                  <date-picker
                    v-model.lazy="Contract.contract_date"
                    :config="options"
                  ></date-picker>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="">Ghi ch hp ng</label>
                  <textarea
                    class="form-control"
                    rows="1"
                    v-model.lazy="Contract.contract_note"
                    placeholder="Nhp ghi ch hp ng"
                  />
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Loi hp ng</label>
                  <select class="form-select" v-model.lazy="Contract.contract_type">
                    <option value="0">ng k mi</option>
                    <option value="1">Gia hn</option>
                  </select>
                </div>
              </div>
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Thng ng k hp ng</label>
                  <select
                    class="form-select"
                    v-model.lazy="Contract.contract_expired_month"
                  >
                    <option :value="item.id" v-for="item in dateChooses" :key="item.id">
                      {{ item.text }}
                    </option>
                  </select>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Ngy bt u</label>
                  <date-picker
                    v-model="Contract.contract_date_start"
                    :config="options"
                  ></date-picker>
                </div>
              </div>
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Ngy ht hn</label>
                  <date-picker
                    v-model="Contract.contract_date_end"
                    :config="options"
                  ></date-picker>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Trng thi hp ng</label>
                  <select class="form-select" v-model="Contract.contract_status">
                    <option value="0">Mi to lp</option>
                    <option value="1"> trnh k</option>
                    <option value="2"> gi khch hng</option>
                    <option value="3">Hon thin</option>
                    <option value="4"> lm thanh ton</option>
                  </select>
                </div>
              </div>
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Trng thi thanh ton</label>
                  <select class="form-select" v-model="Contract.contract_payment">
                    <option value="0">Cha thanh ton</option>
                    <option value="1"> nhn tin nhng cha np tin</option>
                    <option value="2">
                       xut ha n nhng cha thu tin khch hng
                    </option>
                    <option value="3"> np tin</option>
                    <option value="4"> np tin v lm thanh ton</option>
                  </select>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">Khu vc tnh doanh thu</label>
                  <select class="form-select" v-model="Contract.contract_area">
                    <option value="0">Thnh ph</option>
                    <option value="1">Huyn (Khc)</option>
                  </select>
                </div>
              </div>
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">M hp ng/Thanh ton</label>
                  <input
                    v-model="Contract.contract_code"
                    type="text"
                    placeholder="M hp ng/M thanh ton"
                    class="form-control"
                  />
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">M BHXH/S lng ha n</label>
                  <input
                    v-model="Contract.extra1"
                    type="text"
                    placeholder="M BHXH/S lng ha n"
                    class="form-control"
                  />
                </div>
              </div>
              <div class="col-6 col-md-6 col-sm-12 col-xs12">
                <div class="form-group">
                  <label for="">K hiu/Mu s</label>
                  <input
                    v-model="Contract.extra2"
                    type="text"
                    placeholder="K hiu/Mu s"
                    class="form-control"
                  />
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6">
                <div class="form-group">
                  <label for="">T l hp ng</label>

                  <select v-model="Contract.ratio_id" class="form-select">
                    <option value="0">--Hy chn d liu---</option>
                    <option
                      :value="item.ratio_id"
                      v-for="item in ratios"
                      :key="item.ratio_id"
                    >
                      {{ item.ratio_name }}
                    </option>
                  </select>
                </div>
              </div>
            </div>

            <div class="row mt-2">
              <div class="col-12">
                <div class="btn-group mb-2 mt-1 d-inline">
                  <button
                    type="button"
                    class="btn btn-danger dropdown-toggle"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <i class="fa-duotone fa-circle-plus"></i> Thm
                  </button>
                  <ul class="dropdown-menu">
                    <li>
                      <a
                        class="dropdown-item hand"
                        @click="addDetail()"
                        title="Thm dch v"
                        ><i class="fa-thin fa-grid-2-plus"></i> Thm dch v</a
                      >
                    </li>
                    <li>
                      <a
                        class="dropdown-item hand"
                        @click="addTemplates()"
                        title="Chn mu hp ng"
                        ><i class="fa-thin fa-layer-plus"></i> Chn mu hp ng</a
                      >
                    </li>
                    <li>
                      <a
                        class="dropdown-item hand"
                        @click="addRegistration()"
                        title="Thm phiu ng k"
                        ><i class="fa-thin fa-user-plus"></i> Thm phiu ng k</a
                      >
                    </li>
                    <li>
                      <a
                        class="dropdown-item hand"
                        @click="chooseRegistration()"
                        title="Chn phiu ng k"
                      >
                        <i class="fa-light fa-folder-open"></i> Chn phiu ng k</a
                      >
                    </li>
                  </ul>
                </div>

                <router-link
                  class="btn btn-primary-soft ms-2"
                  :to="{
                    name: 'contract-option',
                    params: {
                      id: contract_id,
                    },
                  }"
                >
                  <i class="fa-regular fa-gear"></i> Ty chnh hp ng
                </router-link>

                <div v-if="isUpdate" class="ms-2 d-inline">
                  <span
                    class="badge text-bg-light text-dark border p-2 ms-1"
                    v-for="item in Contract.templates"
                    :key="item.temp_id"
                  >
                    {{ item.temp_name }}
                  </span>
                </div>

                <ul
                  class="nav nav-pills mb-2 bg-light mt-2"
                  id="pills-tab"
                  role="tablist"
                >
                  <li class="nav-item" role="presentation">
                    <button
                      class="nav-link active"
                      id="pills-home-tab"
                      data-bs-toggle="pill"
                      data-bs-target="#pills-home"
                      type="button"
                      role="tab"
                      aria-controls="pills-home"
                      aria-selected="true"
                    >
                      Danh sch dch v
                    </button>
                  </li>
                  <li class="nav-item" role="presentation">
                    <button
                      class="nav-link position-relative"
                      id="pills-profile-tab"
                      data-bs-toggle="pill"
                      data-bs-target="#pills-profile"
                      type="button"
                      role="tab"
                      aria-controls="pills-profile"
                      aria-selected="false"
                    >
                      Danh sch phiu ng k
                      <span
                        class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger"
                      >
                        {{ Contract.registrations.length }}
                      </span>
                    </button>
                  </li>
                </ul>
                <div class="tab-content" id="pills-tabContent">
                  <div
                    class="tab-pane fade show active"
                    id="pills-home"
                    role="tabpanel"
                    aria-labelledby="pills-home-tab"
                    tabindex="0"
                  >
                    <div class="table-responsive table-scroll">
                      <table
                        class="table table-dark-gray align-middle p-4 mb-0 table-hover"
                        v-if="filteredDetails.length > 0"
                      >
                        <thead class="table-dark">
                          <tr>
                            <th class="rounded-start">#</th>
                            <th width="40%">Tn dch v</th>
                            <th width="10%">n v tnh</th>
                            <th width="7%">S lng</th>
                            <th>Tin trc thu</th>
                            <th>Tin sau thu</th>
                            <th class="rounded-end" width="10%"></th>
                          </tr>
                        </thead>
                        <tbody>
                          <contractDetail
                            v-for="(detail, index) in Contract.details"
                            :key="'rows_' + index + '_' + detail.contract_id"
                            v-bind:detail="detail"
                            v-bind:index="index"
                            @sendDetail="sendDetail"
                            @deleteDetail="deleteDetail"
                          />
                        </tbody>
                      </table>
                    </div>
                    <template v-if="filteredDetails.length == 0">
                      <not-found></not-found>
                    </template>
                  </div>
                  <div
                    class="tab-pane fade"
                    id="pills-profile"
                    role="tabpanel"
                    aria-labelledby="pills-profile-tab"
                    tabindex="0"
                  >
                    <template v-if="Contract.registrations.length == 0">
                      <not-found></not-found>
                    </template>
                    <table
                      class="table table-dark-gray align-middle p-4 mb-0 table-hover mt-2"
                      v-if="Contract.registrations.length > 0"
                    >
                      <thead class="table-dark">
                        <tr>
                          <th class="rounded-start">#</th>
                          <th width="45%">H v tn</th>
                          <th>Chc v</th>
                          <th>ST</th>
                          <th>Email</th>
                          <th>M TB</th>
                          <th width="100" class="rounded-end"></th>
                        </tr>
                      </thead>

                      <regDetail
                        v-for="(item, index) in filteredRegistrations"
                        :key="index + 1"
                        v-bind:registration="item"
                        :index="index"
                        @sendRegistration="sendRegistration"
                        @deleteRegistration="deleteRegistration"
                      >
                      </regDetail>
                    </table>

                    <div class="text-center">
                      <button
                        class="btn btn-primary-soft mt-2 btn-sm"
                        @click="chooseRegistration()"
                      >
                        <i class="fa-light fa-folder-open"></i> Chn phiu ng k
                      </button>
                    </div>
                  </div>
                </div>

                <div class="d-flex justify-content-center" v-if="isLoadingTable">
                  <div class="spinner-border" role="status">
                    <span class="visually-hidden">Loading...</span>
                  </div>
                </div>
              </div>
            </div>

            <div class="d-flex align-items-end flex-column">
              <div class="row mt-2">
                <div class="col-auto">
                  <label class="fw-bold">Tin trc thu:</label>
                </div>
                <div class="col-auto">
                  <money
                    v-model="Contract.subTotal"
                    v-bind="money"
                    class="form-control text-end"
                  >
                  </money>
                </div>
              </div>
              <div class="row mt-2">
                <div class="col-auto">
                  <label class="fw-bold col-form-label">Tin thu:</label>
                </div>
                <div class="col-auto">
                  <money
                    v-model.lazy="Contract.contract_tax"
                    v-bind="money"
                    class="form-control text-end"
                    @change.native="changeTax"
                  >
                  </money>
                </div>
              </div>

              <div class="row mt-2">
                <div class="col-auto">
                  <label class="fw-bold col-form-label">Thnh tin:</label>
                </div>
                <div class="col-auto">
                  <money
                    v-model.lazy="Contract.contract_total"
                    v-bind="money"
                    class="form-control text-end"
                  >
                  </money>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for=""> Tin bng ch </label>
                  <input
                    v-model="Contract.contract_price_text"
                    type="text"
                    class="form-control"
                    placeholder="Thnh tin bng ch"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="card-footer border-top-0">
            <router-link
              :to="{
                name: 'contract-index',
              }"
              class="btn btn-secondary"
            >
              <i class="fa-thin fa-arrow-left"></i> Quay li
            </router-link>
            <button class="btn btn-primary-soft ms-1" @click="store()">
              <i class="fa-thin fa-floppy-disk"></i> Lu
            </button>
          </div>
        </div>
      </div>
    </div>
    <contract-option-modal
      ref="contractoption"
      @updateContractOption="updateContractOption"
    ></contract-option-modal>
    <modelAddDetail ref="modalDetail" @saveDetail="saveDetail"></modelAddDetail>
    <modal-templates
      :templateSelecteds="templateSelected"
      ref="modalTemplate"
      @saveTemplate="saveTemplate"
    ></modal-templates>
    <modal-registration
      ref="modalRegistration"
      @saveRegistration="saveRegistration"
    ></modal-registration>
    <modal-companies ref="modalcompanies" @chooseCompany="chooseCompany" />
    <modal-customers ref="modalcustomers" @chooseCustomer="chooseCustomer" />
    <modal-customer-add
      @addCustomerSuccess="addCustomerSuccess"
      ref="modalcustomeradd"
    ></modal-customer-add>
    <modal-registration-list
      ref="registrationList"
      @reviceRegistration="reviceRegistration"
    ></modal-registration-list>
  </div>
</template>

<script>
import axios from "axios";
import Select2 from "v-select2-component";
import contractDetail from "../../components/contracts/Details.vue";
import contractDevice from "../../components/contracts/Devices.vue";
import regDetail from "../../components/contracts/Registrations.vue";
import modelAddDetail from "../../extends/ModalAddDetail.vue";
import modalTemplates from "../../extends/ModalTemplates.vue";
import modalRegistration from "../../extends/ModalRegistration.vue";
import modalCompanies from "../../extends/ModalCompanies.vue";
import modalCustomers from "../../extends/ModalCustomers.vue";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import moment from "moment";
import ModalCustomerAdd from "@/extends/ModalCustomerAdd.vue";
import ModalRegistrationList from "@/extends/ModalRegistrationList.vue";
import { Money } from "v-money";
import notFound from "../../extends/NotFound.vue";
import ContractOptionModal from "@/extends/ModalContractOption.vue";

export default {
  components: {
    Select2,
    contractDetail,
    contractDevice,
    modelAddDetail,
    Money,
    modalTemplates,
    modalRegistration,
    regDetail,
    modalCompanies,
    modalCustomers,
    notFound,
    datePicker,
    ModalCustomerAdd,
    ContractOptionModal,
    ModalRegistrationList,
  },

  data() {
    return {
      dateChoose: 12,
      dateChooses: [
        {
          id: 6,
          text: "06 thng",
        },
        {
          id: 12,
          text: "12 thng",
        },
        {
          id: 18,
          text: "18 thng",
        },
        {
          id: 24,
          text: "24 thng",
        },
        {
          id: 33,
          text: "33 thng",
        },
        {
          id: 36,
          text: "36 thng",
        },
        {
          id: 48,
          text: "48 thng",
        },
        {
          id: 130,
          text: "130 thng (10 nm)",
        },
      ],
      settings: {
        placeholder: "--Hy chn d liu--",
        theme: "bootstrap-5",
      },
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " ",
        precision: 0,
        masked: false,
      },
      ratios: [],
      contract_id: 0,
      isUpdate: false,
      Contract: {
        company_id: 0,
        company_name: "",
        contract_number: "",
        contract_number_pattern: "",
        contract_code: "",
        // contract_date: moment(),
        contract_date: this.$econtract.formatDate(new Date()),
        contract_note: "",
        customer_id: 0,
        customer_name: "",
        contract_type: 0,
        contract_price_text: "",
        contract_date_create: moment(),
        contract_date_start: this.$econtract.formatDate(new Date()),
        contract_date_end: this.$econtract.formatDate(new Date()),
        contract_payment: 0,
        contract_status: 0,
        is_renew: false,
        renew_date: moment(),
        contract_total: 0,
        contract_tax: 0,
        contract_area: 0,
        subTotal: 0,
        contract_expired_month: 12,
        details: [],
        templates: [],
        registrations: [],
        ratio_id: 0,
      },
      selects: {
        companies: [],
        customers: [],
        contract_number_pattern_list: [
          "TTKD VNPT-ST-KHDN/HCA",
          "TTKD VNPT-ST-KHDN/H HT",
          "TTKD VNPT-ST-KHDN/HBHXH",
          "TTKD VNPT-ST-KHDN/H HKD",
          "TTKD VNPT-ST-KHDN/HVANCA",
          "TTKD VNPT-ST-KHDN/HCNTT",
        ],
      },
      Price: {
        SubTotal: 0,
        Tax: 0,
        Total: 0,
        DevicePrice: 0,
        ComponentPrice: 0,
      },
      isLoadingTable: false,
      templateSelected: [],
      debounceTimeout: null,
    };
  },
  watch: {
    "Contract.contract_type": {
      handler: function (newValue) {
        let value = parseInt(newValue);
        this.Contract.ratio_id = value + 1;
      },
    },
    "Contract.contract_total": {
      handler: function (newValue) {
        this.Contract.contract_price_text = this.numberToVND(newValue);
      },
    },
    "Contract.contract_expired_month": {
      handler: function (value) {
        this.changeDateChoose();
      },
    },
    "Contract.contract_date": {
      handler: function (value) {
        this.Contract.contract_date_start = value;
        this.changeDateChoose();
      },
      deep: true,
      immediate: false,
    },
    "Contract.details": {
      handler: function (value) {
        let productPriceTotal = 0;
        let productPrice = this.Contract.details.reduce((sum, result) => {
          if (result.is_payment)
            productPriceTotal =
              sum + result.detail_product_price * result.detail_quantity;
          return productPriceTotal;
        }, 0);

        this.Contract.subTotal = productPrice;
        this.Contract.contract_tax = productPrice * 0.1;
        this.Contract.contract_total = Math.round(
          this.Contract.subTotal + this.Contract.contract_tax
        );

        this.Contract.contract_price_text = this.numberToVND(
          this.Contract.contract_total
        );
      },
      deep: true,
      immediate: false,
    },
  },
  computed: {
    filteredDetails() {
      return this.Contract.details.filter((item) => item.isDelete !== 1);
    },
    filteredRegistrations() {
      return this.Contract.registrations.filter((item) => item.isDelete !== 1);
    },
  },
  created() {
    if (this.$route.params.id) {
      this.isUpdate = true;
      this.contract_id = this.$route.params.id;
      this.getDetail();
    }
    this.changeDateChoose();
    this.getRatio();

    this.Contract.ratio_id = 1;
  },
  activated() {
      this.fet
  },
  methods: {
    reviceRegistration(data) {
      // data.reg_id = 0;
      this.Contract.registrations.push(data);
      this.$toast.success("Thm d liu thnh cng");
    },
    chooseRegistration() {
      this.$refs.registrationList.show();
    },
    updateContractOption(item) {
      this.Contract.option.owner_name = "ac";
    },
    showContractOptionModal() {
      this.$refs.contractoption.show();
    },
    addCustomerSuccess(value) {
      this.Contract.customer_id = value.customer_id;
      this.Contract.customer_name = value.customer_name;
    },
    addCustomer() {
      this.$refs.modalcustomeradd.show();
    },
    changeDateChoose() {
      let dateStart = moment(this.Contract.contract_date_start, "DD/MM/YYYY");
      let currentDate = moment(dateStart, "DD/MM/YYYY"); // Ly ngy hin ti
      let newDate = moment(currentDate).add(
        this.Contract.contract_expired_month,
        "months"
      );
      this.Contract.contract_date_end = newDate.format("DD/MM/YYYY");
    },
    chooseCustomer(item) {
      let phone = item.customer_phone != 0 ? item.customer_phone : "Cha c s lin h";
      this.Contract.customer_name = item.customer_name + " - ST: " + phone;
      this.Contract.customer_id = item.customer_id;
    },
    openModalCustomer() {
      this.$refs.modalcustomers.show();
    },
    chooseCompany(item) {
      this.Contract.company_id = item.company_id;
      this.Contract.company_name = item.company_name + " - MST: " + item.company_taxcode;
    },
    openModalCompany() {
      this.$refs.modalcompanies.show();
    },
    saveRegistration(registration, isUpdate, index) {
      if (isUpdate) {
        Object.assign(this.Contract.registrations[index], registration);
        this.$toast.success("Cp nht thu bao thnh cng");
      } else {
        this.Contract.registrations.push({
          ...registration,
          contract_id: this.Contract.contract_id,
        });
        this.$toast.success("Thm thu bao thnh cng");
      }
    },
    addRegistration() {
      this.$refs.modalRegistration.show();
    },
    saveTemplate(value) {
      this.templateSelected = value;
    },
    addTemplates() {
      this.$refs.modalTemplate.show();
    },
    sendRegistration(value, index) {
      this.$refs.modalRegistration.show(value, index);
    },
    sendDetail(value, index) {
      this.$refs.modalDetail.show(value, index);
    },
    saveDetail(detail, isUpdate, index) {
      if (isUpdate) {
        Object.assign(this.Contract.details[index], detail);
        this.$toast.success("Cp nht thu bao thnh cng");
      } else {
        this.Contract.details.push({
          ...detail,
          detail_id: 0,
          contract_id: this.Contract.contract_id,
          detail_note: typeof detail.detail_note == undefined ? null : detail.detail_note,
          // product: [],
          // category: [],
          detail_device_price: 0,
        });
        this.$toast.success("Thm thu bao thnh cng");
      }
    },
    addDetail() {
      this.$refs.modalDetail.show();
    },

    async store() {
      clearTimeout(this.debounceTimeout);

      if (this.Contract.contract_number.length == 0) {
        this.$toast.warning("Cha nhp s hp ng");
        return false;
      }

      delete this.Contract.company_name;
      delete this.Contract.customer_name;

      if (this.templateSelected.length == 0) {
        this.$toast.warning("Bn cha chn mu hp ng");
        return false;
      }

      this.debounceTimeout = setTimeout(async () => {
        this.$econtract.showLoading();

        this.Contract.templates = this.templateSelected;

        try {
          let response;
          if (this.isUpdate)
            response = await axios.put(`/contracts/update`, this.Contract);
          else response = await axios.post(`/contracts/store`, this.Contract);

          if (response.status == 200) {
            this.$toast.success(
              // this.isUpdate ? "Cp nht hp ng thnh cng" : "Thm hp ng thnh cng"
              response.data.msg
            );
            this.$router.push({ name: "contract-index" });
          }
        } catch (error) {
          this.$toast.error("C li xy ra khi to mi hp ng");
        } finally {
          this.$econtract.hideLoading();
        }

        // if (this.isUpdate) {
        //   await axios.put(`/contracts/update`, this.Contract).then((response) => {
        //     if (response.status == 200) {
        //       this.$econtract.hideLoading();
        //       this.$toast.success("Cp nht hp ng thnh cng");
        //     }
        //   });
        // } else {
        //   await axios.post(`/contracts/store`, this.Contract).then((response) => {
        //     if (response.status == 200) {
        //       this.$econtract.hideLoading();
        //       this.$toast.success("Thm hp ng thnh cng");
        //     }
        //   });
        // }

        // this.$router.push({
        //   name: "contract-index",
        // });
      }, 300);
    },

    validate() {
      if (this.Contract.contract_number.length == 0) {
        this.$toast.warning("Cha nhp s hp ng");
        return false;
      }
    },

    changeSubTotal() {
      this.Contract.subTotal = this.Contract.subTotal;
    },

    changeTax() {
      this.Contract.contract_total = Math.round(
        this.Contract.subTotal + this.Contract.contract_tax
      );
    },

    deleteDetail(detail, index) {
      this.$confirm({
        title: "Xc nhn xa",
        message: "Bn c mun xa dng ny khng?",
        button: {
          yes: "Yes",
          no: "Cancel",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$set(this.Contract.details, index, {
              ...this.Contract.details[index],
              isDelete: 1,
            });
            this.$toast.success("Xa dch v thnh cng");
          }
        },
      });
    },
    deleteRegistration(registration, index) {
      this.$confirm({
        title: "Xc nhn xa",
        message: "Bn c mun xa dng ny khng?",
        button: {
          yes: "Yes",
          no: "Cancel",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$set(this.Contract.registrations, index, {
              ...this.Contract.registrations[index],
              isDelete: 1,
            });
            this.Contract.registrations.splice(index, 1);
            this.$toast.success("Xa dch v thnh cng");
          }
        },
      });
    },
    formattedDate(date) {
      return date;
    },
    async getRatio() {
      await axios.get(`/ratio`).then((response) => {
        this.ratios = response.data;
      });
    },
    async getDetail() {
      clearTimeout(this.debounceTimeout);
      this.isLoadingTable = true;
      this.$econtract.showLoading();

      this.debounceTimeout = setTimeout(async () => {
        await axios.get(`/contracts/edit/${this.contract_id}`).then((response) => {
          if (response.data.status_code == 500) {
            this.$toast.warning(response.data.msg);
            this.$router.push({
              name: "contract-index",
            });
          } else {
            this.Contract = response.data;
            this.Contract.templates.map((template) => {
              this.templateSelected.push(template.temp_id);
            });
            this.Contract.contract_date = this.$econtract.formatDate(
              this.Contract.contract_date
            );
            this.Contract.contract_date_start = this.$econtract.formatDate(
              this.Contract.contract_date_start
            );
            this.Contract.contract_date_end = this.$econtract.formatDate(
              this.Contract.contract_date_end
            );
            this.$econtract.hideLoading();
            this.isLoadingTable = false;
          }
        });
      }, 300);
    },
    numberToVND(number) {
      return this.$econtract.doc(number);
    },
  },
};
</script>
