<template>
  <b-modal
    id="modal-contract-option"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="lg"
    title="Ty chnh thng tin hp ng"
  >
    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">Ngi i din</label>
          <input
            type="text"
            placeholder="Tn ngi i  din"
            v-model="ContractOption.owner_name"
            class="form-control"
          />
        </div>
      </div>
      <div class="col-6">
        <div class="form-group">
          <label for="">Chc v</label>
          <input
            type="text"
            placeholder="Chc v i  din"
            v-model="ContractOption.owner_position"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">S CCCD</label>
          <input
            type="text"
            placeholder="CCCD/CMND Ngi i din"
            v-model="ContractOption.owner_card_id"
            class="form-control"
          />
        </div>
      </div>
      <div class="col-6">
        <div class="form-group">
          <label for="">Ngy Cp</label>
          <input
            type="text"
            placeholder="Ngy cp"
            v-model="ContractOption.owner_card_date"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <div class="form-group">
          <label for="">Ni cp</label>
          <input
            type="text"
            placeholder="Ni cp CCCD"
            v-model="ContractOption.owner_card_issuedby"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <div class="form-group">
          <label for="">Giy y quyn s:</label>
          <input
            type="text"
            placeholder="Giy y quyn s:"
            v-model="ContractOption.owner_authority"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <template #modal-footer>
      <button @click="store" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lu
      </button>
      <button class="btn btn-secondary" @click="close">
        <i class="fa-thin fa-xmark"></i> ng
      </button>
    </template>
  </b-modal>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      ContractOption: {
        owner_name: "",
        owner_position: "",
        owner_authority: "",
        contract_id: 0,
        owner_card_issuedby: "",
        owner_card_date: moment().format("DD/MM/YYYY"),
        owner_card_id: 0,
      },
    };
  },
  methods: {
    store() {
      this.$emit("updateContractOption", this.ContractOption);
      this.close();
    },
    show() {
      this.$bvModal.show("modal-contract-option");
    },
    close() {
      this.$bvModal.hide("modal-contract-option");
    },
  },
};
</script>
